
import React , { useState } from "react";
import axios from "axios";
import '../../styles/workout.css';

const Workout = ({ email }) => {
    const [ pushupCount, setPushupCount] = useState(0);
    const [achievement, setAchievement] = useState('');

    const completeWorkout = async () => {
        try{
            const response = await axios.post('http://localhost:5001/completeWorkout', {
                email,
                pushupCount
            });
            setAchievement(response.data.achievement);
        }catch (error){
            console.error("Error completing workout", error);
            alert('Error completing workout: '+ error.response.data);        
        }
    }; 
    
    return (
        <div className="workout">
            <h2>PushUp Workout</h2>
            <label>
                Number of Pushups : 
                <input 
                type=" number"
                value={pushupCount} 
                onChange={(e) => setPushupCount(e.target.value)}
                />
            </label>
            <button className='work-btn' onClick={completeWorkout}>Complete Workout</button>
            {achievement && <p>Achievement Unlocked: {achievement}</p>}
        </div>
    );
    
};

export default Workout;