import React, { useEffect} from 'react';
import '../../styles/login.css';


const AuthWrapper = () => {
  useEffect(() => {
    const loginWrapper = document.querySelector('.login__wrapper');
    const loginLink = document.querySelector('.login__link a');
    const registerLink = document.querySelector('.register__link a');
    const btnPopup = document.querySelector('.btnLogin-popup');
    const icon__close = document.querySelector('.icon__close');
    
 
    if (registerLink){
    registerLink.addEventListener('click', () => {
      loginWrapper.classList.add('active');
    });
  }

  if (loginLink){
    loginLink.addEventListener('click', () => {
      loginWrapper.classList.remove('active');
    });
  }

  if (btnPopup){
    btnPopup.addEventListener('click', () => {
      loginWrapper.classList.add('active-popoup');
    });
  }

  if (icon__close){
    icon__close.addEventListener('click', () => {
      loginWrapper.classList.remove('active-popoup');
    });
  }

  return () => {
    if (registerLink){
      registerLink.removeEventListener('click', () => {
        loginWrapper.classlist.add('active');
      });
    }

    if (loginLink){
      loginLink.removeEventListener('click', () => {
        loginWrapper.classlist.remove('active');
      });
    }

    if (btnPopup){
      btnPopup.removeEventListener('click', () => {
        loginWrapper.classlist.add('active-popup');
      });
    }

    if (icon__close){
      icon__close.removeEventListener('click', () => {
        loginWrapper.classlist.remove('active-popup');
      });
    }
  };

   
  }, []);

  return null;
};

export default AuthWrapper;

