import React, { useState } from 'react'
import { Dumbbell } from "lucide-react";
import Label from '../../components/Label/Label';
import Input from '../../components/Input/Input';
import { Button } from '@mui/material';

const BMI = () => {
    
 //logic to write bmi 
  const [weight, setWeight] = useState('');
  const [height, setHeight] = useState('');
  const [bmi, setBmi] = useState(null);

  //function to calculate bmi
  const calculateBMI = () => {
    const bmi = (weight / ((height * height) / 10000)).toFixed(2);
    setBmi(bmi);
  };
  
    return <section id='bmi'>(
    <div alignItems='center' data-aos='fade-up' style={{ width:'100%', height:'auto' }}>
        <div  style={{ width: '100%', height: 'auto', paddingLeft: '250px' ,
         paddingRight: '250px',  paddingTop: '40px',  paddingBottom: '40px', display:'flex', alignItems: 'center', 
        flexDirection: 'column', justifyContent:'space-between' }}>
            
            <h2  style={{ fontsize: '1rem', fontWeight: 500,  color: 'black', 
            display: 'flex', alignItems: 'center',  gap: '1rem', backgroundColor:'rgba(97,158,215)',  
            borderTopLeftRadius: '0.375rem', borderTopRightRadius: '0.375rem', padding: '0.5rem 5rem' }}>
            
            <Dumbbell style={{ width:'1rem', height:'2.5rem', transform:'rotate(-45deg)', 
            color:'#4f46e5' }}/>
                Calculate Your BMI
            </h2>
            
            <div style={{ width:' 90%', height:'auto', display:'flex', alignItems:'flex-end', 
            justifyContent:'center', backgroundColor:'rgba(177,215,235)', borderRadius:'1.375rem', 
            gap:'0.5rem', padding:'2.5rem 1rem' , flexWrap:'wrap'}} 
               
               > <div style={{ gap: {
                sm: '122px' , xs: '40px'}, mt: { sm: '32px', xs:'20px'} , 
                width:'auto', height:'40px', padding:'0.5rem' }} 
                >
                    <Label htmlFor='weight'>Weight (kg) </Label>
                    <Input 
                    id='weight' 
                    name='weight' 
                    type='number' 
                    placeholder='Enter your weight'
                    value={weight}
                    onChange={(e) => setWeight(e.target.value)}>
                    </Input>
                </div>
            
            <div style={{ width:'auto', height:'40px', padding:'0.5rem'}} >
                    <Label htmlFor='weight'>Height (cm) </Label>
                    <input 
                    id='height' 
                    name='height' 
                    type='number' 
                    placeholder='Enter your height'
                    value={height}
                    onChange={(e) => setHeight(e.target.value)}>
                    </input>
                </div>
                <div style={{ width:'auto', height: 'auto' , padding:'0.5rem'}} >
                    <Button variant='contained'
                    style={{ width:'100%', height: '2rem', justifyContent:'center'}} onClick={calculateBMI}>Calculate BMI</Button>
                    
                </div>
        </div>
       
        {/* Display BMI */}
        {bmi !== null && (
            <div style={{ background:'black', borderRadius:'0 0 0.375rem 0.375rem', paddingLeft:'1.5rem', paddingRight:'1.5rem', paddingTop:'0.625rem', paddingBottom:'0.625rem' }}>
                <p style={{ color:'rgba(8,204,48)'}}>
                    Your BMI: <span className='font-bold'>{bmi}</span>
                </p>
            </div>
        )}
       
        </div>
    </div>
  )
  </section>
}

export default BMI